import React, { Component } from 'react';
import Matrix, {isAllocatedHere} from './Matrix';
import {SCHEMA, NODES, schemaToTree, updatePartitionConfig} from './data';
import './App.css';
import PartitionConfigEditor from './PartitionConfigEditor';

function flatten(nodeTree) {
  const output = [];
  function recur(node, attrsSoFar, depth) {
    const attrsCopy = Object.assign({}, attrsSoFar);
    if (node.key && node.value) {
      attrsCopy[node.key] = node.value;
    }
    output.push({
      type: node.children ? 'LOCALITY' : 'NODE',
      id: node.node_id,
      attrs: attrsCopy,
      key: node.key,
      value: node.value,
      depth,
    });
    if (node.children) {
      for (let child of node.children) {
        recur(child, attrsCopy, depth + 1);
      }
    }
  }
  recur(nodeTree, {}, 0);
  return output;
}

const flattenedNodes = flatten(NODES);

class App extends Component {

  constructor() {
    super();
    this.state = {
      selectedTableName: null,
      schema: SCHEMA,
    };
  }

  handleSelectTable(tableName) {
    this.setState({
      selectedTableName: tableName,
    });
  }

  handleSelectCol(path) {
    if (path.length === 2) {
      this.handleSelectTable(path[1]);
    }
  }

  handleUpdatePartitionConfig(tableName, newConfig) {
    this.setState({
      selectedTableName: null,
      schema: updatePartitionConfig(this.state.schema, tableName, newConfig),
    });
  }

  render() {
    const { schema, selectedTableName } = this.state;

    const handleSelectCol = this.handleSelectCol.bind(this);
    const handleUpdatePartitionConfig = this.handleUpdatePartitionConfig.bind(this);

    let body;
    if (selectedTableName === null) {
      body = (
        <Matrix
          flattenedRows={flattenedNodes}
          cols={schemaToTree(schema)}
          onSelectCol={handleSelectCol}
          getCellValue={isAllocatedHere}
        />
      );
    } else {
      const selectedTableNode = schema.databases[0].tables.find((t) => t.name === selectedTableName);

      body = (
        <div>
          <h2>Config for {selectedTableName}</h2>
          <a
            onClick={() => this.handleSelectTable(null)}
            style={{ cursor: 'pointer' }}
          >
            &lt; Back
          </a>
          <PartitionConfigEditor
            flattenedNodes={flattenedNodes}
            table={selectedTableNode}
            onCancel={() => this.handleSelectTable(null)}
            onSave={(newPartitionConfig) =>
              handleUpdatePartitionConfig(selectedTableName, newPartitionConfig)
            }
          />
        </div>
      );
    }

    return (
      <div className="App">
        <h1>Cockroach Overview</h1>

        {body}
      </div>
    );
  }
}

export default App;



