const defaultPartitionConfig = {
  partitionColumn: 'id',
  partitions: [
    {
      name: 'default',
      startValue: '',
      endValue: '',
      zoneConfig: {
        replicationFactor: 3,
        constraints: [
          {
            key: 'country',
            value: 'US',
          }
        ],
      },
    },
  ],
};



export const SCHEMA = {
  databases: [
    {
      name: 'roachblog',
      tables: [
        {
          name: 'users',
          partitionConfig: defaultPartitionConfig,
        },
        {
          name: 'blog_posts',
          partitionConfig: defaultPartitionConfig,
        },
        {
          name: 'comments',
          partitionConfig: defaultPartitionConfig,
        },
      ]
    }
  ]
};

export function schemaToTree(schema) {
  return {
    children: schema.databases.map((db) => ({
      name: db.name,
      children: db.tables.map(tableToTree),
    })),
  }
}

export function tableToTree(table) {
  return {
    name: table.name,
    children: table.partitionConfig.partitions
  };
}

// returns a new schema
export function updatePartitionConfig(oldSchema, tableName, newConfig) {
  return {
    databases: [
      {
        name: 'roachblog', // TODO: don't hardcode this
        tables: oldSchema.databases[0].tables.map((table) => (
          table.name === tableName
            ? {...table, partitionConfig: newConfig}
            : table
        )),
      },
    ],
  };
}

export const NODES = {
  children: [
    {
      key: 'country',
      value: 'US',
      children: [
        {
          key: 'datacenter',
          value: 'us-east',
          children: [
            { node_id: 1 },
            { node_id: 2 },
            { node_id: 3 },
          ]
        },
        {
          key: 'datacenter',
          value: 'us-west',
          children: [
            { node_id: 4 },
            { node_id: 5 },
            { node_id: 6 },
          ]
        }
      ]
    },
    {
      key: 'country',
      value: 'EU',
      children: [
        {
          key: 'datacenter',
          value: 'eu-east',
          children: [
            { node_id: 7 },
            { node_id: 8 },
          ]
        }
      ]
    },
  ]
};



