import _ from 'lodash';

export function getLeaves(tree) {
  const output = [];
  let maxDepth = 0;
  function recur(node, depth) {
    if (!node.children) {
      output.push(node);
      return;
    }
    if (depth > maxDepth) {
      maxDepth = depth;
    }
    for (let child of node.children) {
      recur(child, 0);
    }
  }
  recur(tree, 0);
  return output;
}

// tree => { width, depth, tree }
export function layoutTree(tree) {
  function recur(node, pathToThis) {
    if (!node.children) {
      return [
        [
          {
            width: 1,
            depth: 1,
            name: node.name,
            path: pathToThis,
          }
        ]
      ];
    }

    const childLayouts = node.children.map((childNode) => (
      recur(childNode, [...pathToThis, childNode.name])
    ));
    const maxDepth = _.maxBy(childLayouts, (cl) => cl[0][0].depth)[0][0].depth;
    const transposedChildLayouts = _.range(maxDepth).map(() => ([]));

    for (let childLayout of childLayouts) {
      for (let rowIdx in childLayout) {
        const row = childLayout[rowIdx];
        for (let colIdx in row) {
          const col = row[colIdx];

          transposedChildLayouts[rowIdx].push(col);
        }
      }
    }
    return [
      [
        {
          width: _.sumBy(childLayouts, (cl) => cl[0][0].width),
          depth: maxDepth + 1,
          name: node.name,
          path: pathToThis,
        }
      ],
      ...transposedChildLayouts
    ];
  }

  return recur(tree, []);
}



