import _ from 'lodash';

const REPL_FACTOR = 3; // TODO: get from zone config

function randomNotInSet(taken, rangeSize) {
  if (taken.length === rangeSize) {
    throw new Error('all values taken');
  }
  while (true) {
    const random = Math.round(Math.random() * rangeSize);
    if (!taken.has(random)) {
      return random;
    }
  }
}

function chooseNRandomIndices(n, length) {
  const taken = new Set();
  const output = [];
  for (let i = 0; i < n; i++) {
    const choice = randomNotInSet(taken, length);
    taken.add(choice);
    output.push(choice);
  }
  return output;
}

export function allocate(table, flattenedNodes) {
  return _.range(table.numRanges).map(() => {
    const choices = chooseNRandomIndices(REPL_FACTOR, flattenedNodes.length).map((x) => x + 1);
    return {
      leader: choices[0],
      followers: choices.slice(1),
    }
  });
}

export function fulfillsConstraints(node, constraints) {
  for (let constraint of constraints) {
    if (node.attrs[constraint.key] !== constraint.value) {
      return false;
    }
  }
  return true;
}



