import _ from 'lodash';
import React, { Component } from 'react';
import classNames from 'classnames';
import './Editor.css';

export const string = {
  type: 'STRING',
};

export function validatedString(fun) {
  return {
    ...string,
    validate: fun,
  };
}

export const int = {
  type: 'INT',
};

export function validatedInt(fun) {
  return {
    ...int,
    validate: fun,
  };
}

export function oneOf(options) {
  return {
    type: 'ONEOF',
    options,
  };
}

export function arrayOf(elementType) {
  return {
    type: 'ARRAY',
    elementType,
  };
}

export function objectOf(fields) {
  return {
    type: 'OBJECT',
    fields,
  };
}

export function emptyValueFor(ty) {
  switch (ty.type) {
    case 'STRING':
      return '';

    case 'INT':
      return 0;

    case 'ONEOF':
      return ty.options[0];

    case 'ARRAY':
      return [];

    case 'OBJECT':
      return _.mapValues(ty.fields, emptyValueFor);

    default:
      throw new Error('uknown type', ty);
  }
}

export function isValid(config, value) {
  switch (config.type) {
    case 'INT':
    case 'STRING': {
      return config.validate
        ? config.validate(value)
        : true;
    }
    case 'OBJECT':
      return _.every(_.map(value, (v, k) => (isValid(config.fields[k], v))));
    case 'ARRAY':
      return _.every(value.map((elem) => isValid(config.elementType, elem)));
    default:
      return true;
  }
}

function swap(arr, idxA, idxB) {
  const copy = [...arr];
  copy[idxA] = arr[idxB];
  copy[idxB] = arr[idxA];
  return copy;
}

class Editor extends Component {

  handleChange(val) {
    this.props.onChange(val);
  }

  handleArrayChange(idx, val) {
    const valCopy = [...this.props.value];
    valCopy[idx] = val;
    this.props.onChange(valCopy);
  }

  handleArrayAdd() {
    this.setState({
      arrayKeys: [...this.state.arrayKeys, this.state.nextId],
      nextId: this.state.nextId + 1,
    });

    this.props.onChange([
      ...this.props.value,
      emptyValueFor(this.props.config.elementType)
    ]);
  }

  handleArrayRemove(idx) {
    const arrayKeysCopy = [...this.state.arrayKeys];
    arrayKeysCopy.splice(idx, 1);
    this.setState({
      arrayKeys: arrayKeysCopy,
    });

    const valCopy = [...this.props.value];
    valCopy.splice(idx, 1);
    this.props.onChange(valCopy);
  }

  handleObjectChange(key, val) {
    const valCopy = _.clone(this.props.value);
    valCopy[key] = val;
    this.props.onChange(valCopy);
  }

  handleArrayMove(idx, dir) {
    const toIdx = dir === 'UP' ? idx - 1 : idx + 1;
    const newValue = swap(this.props.value, idx, toIdx);
    this.handleChange(newValue);
    this.setState({
      arrayKeys: swap(this.state.arrayKeys, idx, toIdx),
    });
  }

  componentWillMount() {
    if (this.props.config.type === 'ARRAY') {
      this.setState({
        arrayKeys: this.props.value.map((elem, idx) => idx),
        nextId: this.props.value.length,
      });
    }
  }

  render() {
    const { config, value } = this.props;

    const handleChange = this.handleChange.bind(this);
    const handleArrayAdd = this.handleArrayAdd.bind(this);
    const handleArrayRemove = this.handleArrayRemove.bind(this);
    const handleArrayChange = this.handleArrayChange.bind(this);
    const handleObjectChange = this.handleObjectChange.bind(this);
    const handleArrayMove = this.handleArrayMove.bind(this);

    const isValid = config.validate ? config.validate(value) : true;

    switch (config.type) {
      case 'STRING': {
        return (
          <input
            type="text"
            className={classNames({invalid: !isValid})}
            onChange={(evt) => handleChange(evt.target.value)}
            value={value}
          />
        );
      }
      case 'INT':
        return (
          <input
            type="number"
            className={classNames({invalid: !isValid})}
            onChange={(evt) => handleChange(evt.target.value)}
            value={value}
          />
        );

      case 'ONEOF':
        return (
          <select value={value} onChange={(evt) => handleChange(evt.target.value)}>
            {config.options.map((option) => (
              <option key={option}>{option}</option>
            ))}
          </select>
        );

      case 'ARRAY':
        return (
          <ol>
            {value.map((element, idx) => (
              <li key={this.state.arrayKeys[idx]}>
                <button
                  onClick={() => handleArrayRemove(idx)}
                >
                  x
                </button>
                <button
                  onClick={() => handleArrayMove(idx, 'UP')}
                  disabled={idx === 0}
                >
                  ^
                </button>
                <button
                  onClick={() => handleArrayMove(idx, 'DOWN')}
                  disabled={idx === value.length - 1}
                >
                  v
                </button>
                <Editor
                  config={config.elementType}
                  value={element}
                  onChange={(val) => handleArrayChange(idx, val)}
                />
              </li>
            ))}
            <li>
              <button onClick={handleArrayAdd}>+</button>
            </li>
          </ol>
        );

      case 'OBJECT':
        return (
          <table className="object-editor">
            <tbody>
              {_.map(config.fields, (fieldType, fieldName) => (
                <tr key={fieldName}>
                  <th>{fieldName}:</th>
                  <td>
                    <Editor
                      config={fieldType}
                      value={value[fieldName]}
                      onChange={(val) => handleObjectChange(fieldName, val)}
                    />
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        );

      default:
        throw new Error('uknown type', config);
    }
  }

}

export default Editor;



