import _ from 'lodash';
import React, { Component } from 'react';
import Matrix, { isAllocatedHere } from './Matrix';
import Editor, { isValid } from './Editor';
import { partitionConfigType } from './editorTypes';
import './PartitionConfigEditor.css';
import { tableToTree } from './data';

class ZoneConfigEditor extends Component {

  componentWillMount() {
    this.setState({
      newPartitionConfig: this.props.table.partitionConfig,
    });
  }

  handleUpdatePartitionConfig(val) {
    this.setState({
      newPartitionConfig: val,
    });
  }

  handleCancel() {
    this.props.handleCancel();
  }

  handleSave() {
    this.props.onSave(this.state.newPartitionConfig);
  }

  render() {
    const { table, flattenedNodes } = this.props;
    const { newPartitionConfig } = this.state;
    const initialPartitionConfig = this.props.table.partitionConfig;

    const newCols = tableToTree({
      ...table,
      partitionConfig: newPartitionConfig,
    });

    const handleUpdatePartitionConfig = this.handleUpdatePartitionConfig.bind(this);
    const handleCancel = this.handleCancel.bind(this);
    const handleSave = this.handleSave.bind(this);

    return (
      <div>
        <h3>Allocation Preview</h3>
        <Matrix
          flattenedRows={flattenedNodes}
          cols={newCols}
          getCellValue={isAllocatedHere}
        />
        <h3>Partition Config</h3>
        <Editor
          config={partitionConfigType}
          value={newPartitionConfig}
          onChange={(val) => handleUpdatePartitionConfig(val)}
        />
        <div>
          <a
            style={{cursor: 'pointer'}}
            onClick={handleCancel}
          >
            cancel
          </a>
          {' '}
          <button
            style={{fontSize: 20}}
            onClick={handleSave}
            disabled={
              _.isEqual(initialPartitionConfig, newPartitionConfig)
              || !isValid(partitionConfigType, newPartitionConfig)
            }
          >
            Save
          </button>
        </div>
      </div>
    );
  }

}

export default ZoneConfigEditor;



