import {string, arrayOf, objectOf, oneOf, validatedInt, validatedString} from './Editor';

export const constraintType = objectOf({
  key: oneOf(['datacenter', 'country']),
  value: validatedString((n) => n.length > 0),
});

export const zoneConfigType = objectOf({
  replicationFactor: validatedInt((x) => x > 0),
  constraints: arrayOf(constraintType),
});

export const partitionType = objectOf({
  name: validatedString((x) => x.length > 0),
  startValue: string,
  endValue: string,
  zoneConfig: zoneConfigType,
});

export const partitionConfigType = objectOf({
  partitionColumn: string,
  partitions: arrayOf(partitionType),
});



