import React, { Component } from 'react';
import { layoutTree, getLeaves } from './tree';
import './Matrix.css';
import {fulfillsConstraints} from './allocate';

const DOWN_ARROW = '▼';

class Matrix extends Component {

  static defaultProps = {
    showRowHeaders: true,
  };

  handleSelectCol(path) {
    if (this.props.onSelectCol) {
      this.props.onSelectCol(path);
    }
  }

  render() {
    const { cols, flattenedRows, showRowHeaders } = this.props;

    const headerRows = layoutTree(cols);
    const flattenedCols = getLeaves(cols);

    const handleSelectCol = this.handleSelectCol.bind(this);

    return (
      <table className="replica-matrix">
        <thead>
          {headerRows.map((row, idx) => (
            <tr key={idx}>
              {showRowHeaders
                ? <td />
                : null}
              {row.map((col) => (
                <td
                  key={col.path}
                  colSpan={col.width}
                  onClick={() => handleSelectCol(col.path)}
                >
                  {col.name}
                </td>
              ))}
            </tr>
          ))}
        </thead>
        <tbody>
          {flattenedRows.filter((n) => n.depth > 0).map((row) => (
            <tr key={JSON.stringify(row)}>
              {showRowHeaders
                ? <th
                    style={{
                      paddingLeft: (row.depth - 1) * 20,
                      textAlign: row.type === 'LOCALITY' ? 'left' : 'right',
                    }}
                >
                    {row.type === 'LOCALITY'
                      ? `${DOWN_ARROW} ${row.key}=${row.value}`
                      : `Node ${row.id}`}
                  </th>
                : null}
              {flattenedCols.map((col, idx) => {
                return (
                  <td key={idx}>
                    {this.props.getCellValue(row, col)}
                  </td>
                );
              })}
            </tr>
          ))}
        </tbody>
      </table>
    )
  }

}

export function isAllocatedHere(node, partitionConfig) {
  if (node.type === 'LOCALITY') {
    return '';
  }

  const res = fulfillsConstraints(node, partitionConfig.zoneConfig.constraints);
  return res ? 'X' : '';
}

export default Matrix;



